.container {
    width: 1060px;
    min-height: 20px;
    margin: 100px auto 0;
    columns: 2;
    column-gap: 20px;
    position: relative;
    .block{
        margin-top: 20px;
        &:nth-child(1){
            margin-top: 0;
        }
    }
    .vector{
        position: absolute;   
        z-index: -1;
        top: -200px;
        left: -200px;
        }
}
@media screen and (max-width: 1100px) {
    .container {
        width: 100%;
        padding: 0 20px;
    }
}
@media screen and (max-width: 767px){
    .container{
        columns: 1;
        display: flex;
        align-items: center;
        flex-direction: column;
    }
}